const PROM = [
    { id: 1, category: "/", title: " ", date: "10 ", link: "/", src: "/assets/images/prom01.jpg" },
    { id: 2, category: "/", title: " ", date: "", link: "/", src: "/assets/images/prom02.jpg" },
    { id: 3, category: "/", title: " ", date: "9 ", link: "/", src: "/assets/images/prom03.jpg" },
    { id: 4, category: "", title: " ", date: "8 ", link: "/", src: "/assets/images/prom04.jpg" },
    { id: 5, category: "", title: " ", date: "  ", link: "/", src: "/assets/images/prom05.jpg" },
    { id: 6, category: "", title: "  ", date: " ", link: "/", src: "/assets/images/prom06.jpg" },

]


const Prom = () => {
    return (
        <section className="Prom">


            <div className="inner">
                {
                    PROM.map(PROM => {
                        return (
                            <figure key={PROM.id}>
                                <a href={PROM.link}>
                                    <div className="box">
                                        <img src={process.env.PUBLIC_URL + PROM.src} alt={PROM.title} />
                                    </div>
                                    <strong>{PROM.category}</strong>
                                    <div className="title">{PROM.title}</div>
                                    <span>{PROM.date}</span>
                                </a>
                            </figure>
                        )
                    })
                }
            </div>
        </section>
    )
}

export default Prom;