import { useRef, useState } from "react";
import Slider from "react-slick";
import "slick-carousel/slick/slick.css";
import "../css/MainSlider.scss";

const MS = [
  {
    id: 1,
    content: " ",
    description:
      "SINSIBOOK          .",
    link: "/",
  },
  {
    id: 2,
    content: " ",
    description:
      "    .      ,    .",
    link: "/",
  },
  {
    id: 3,
    content: " ",
    description:
      "SINSIWAY            . ",
    link: "/",
  },
  {
    id: 4,
    content: " ",
    description:
      "Warm-hearted professtional innovator.           SINSIWAY . ",
    link: "/",
  },
];
const MainSlider = () => {
  const [slideNum, setSlideNum] = useState(0);
  const SLIDE = useRef(null);
  return (
    <section className="MainSlider">
      <div className="container">
        <Slider
          centerMode={true}
          centerPadding={"300px"}
          afterChange={(index) => setSlideNum(index)}
          arrows={false}
          ref={SLIDE}
          className={"MainSlider_slide"}
        >
          {MS.map((img, idx) => {
            return (
              <figure key={img.id} className={slideNum === idx ? "on" : ""}>
                <img
                  src={
                    process.env.PUBLIC_URL +
                    "/assets/images/main_ss0" +
                    img.id +
                    ".jpg"
                  }
                  alt=""
                />
              </figure>
            );
          })}
        </Slider>

        <div className="box">
          <div className="content">
            <div className="tit">{MS[slideNum].content}</div>
            <p>{MS[slideNum].description}</p>
            <a href={MS[slideNum].link} className="cbtn">
              
            </a>
          </div>
          <div className="num">
            0{slideNum + 1} <span>/ 0{MS.length}</span>
          </div>
        </div>
        <div className="arrows">
          <i
            className="xi-angle-left-thin"
            onClick={() => SLIDE.current.slickPrev()}
          ></i>
          <i
            className="xi-angle-right-thin"
            onClick={() => SLIDE.current.slickNext()}
          ></i>
        </div>
        <ul className="dots">
          {MS.map((dot, idx) => (
            <li
              key={dot.id}
              onClick={() => SLIDE.current.slickGoTo(idx)}
              className={slideNum === idx ? "on" : ""}
            >
              {dot.id}
            </li>
          ))}
        </ul>
      </div>

      <div class="boxx">
        <div class="wave -one"></div>
        <div class="wave -two"></div>
        <div class="wave -three"></div>
      </div>
    </section>
  );
};

export default MainSlider;
